.header__link {
   font-weight: 400;
   font-size: 18px;
   line-height: 22px;
   text-align: right;
   color: #ffffff;
   text-decoration: none;
   margin-top: 6px;
   cursor: pointer;
}

.header__link:hover {
   opacity: .6;
}

@media screen and (max-width: 767px) {
   .header__link {
      font-size: 14px;
      line-height: 17px;
   }
}
